    font-family: blackchancery;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff2');
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
body{
    display: grid;
    grid-template: 100px 1fr 100px / min(400px, 20vw) 8fr min(400px, 20vw);
    height: 100vh;
    width: 100vw;
    margin: 0;

}

nav{
    grid-area: 1 / 1 / 2/ 4;
    display: flex;
    padding: 5px 25px;
    align-items: center;
    gap: 15px;
    margin-bottom: 20px;
}
.logo{
    display: flex;
    align-items: center;
    font-size: 40px;
    gap: 8px;
    font-weight: 700;
    font-family: blackchancery;
    margin-right: auto;
}
.logo>img{
    height: 70px;
}
.nav-button{
    display: flex;
    align-items: center;
    height: 40%;
    border-radius: 10px;
    padding: 5px 10px;
    cursor: pointer;
    font-size: 13px;
    gap: 2px;
    background-color: inherit;
    border: 2px solid black;
}
.nav-button:hover{
    border: 3px solid black;
}
.nav-button:active{
    background-color: #eeeeee;
}
.nav-button>img{
    height: 90%;
}

.sidebar{
    grid-area: 2 / 1 / 4 / 2;
    padding: 0 0 100px 0;
    display: grid;
    grid-template: 1fr / 1fr 1fr;
    justify-content: center;
}

.trail{
    text-align: center;
    padding: 10px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    border: 1px solid black;
    border-radius: 0 15px 15px 0;
    background-color: #f7f4f2;
    height: fit-content;
}
.comp{
    overflow: hidden;
    max-width: 0;
    border: 0;
    transition: max-width 1s;
}

.trail>div:first-child{
    font-size: 20px;
    font-family: sans-serif;
    font-weight: 600;
    padding: 10px;
    border-bottom: 1px solid black;
}
.route{
    line-height: 1.6rem;
    padding: 5px;
    width: 10ch;
    font-size: 18px;
}
.comp.active{
    max-width: 100%;
    border: 1px solid black;
    border-radius: 0 15px 15px 0;
    border-left: 0;
}
.user.active{
    border-radius: 0;
}

.result{
    grid-area: 3 / 2 / 4 / 3;
    text-align: center;
    padding: 20px;
    font-size: 20px;
    font-weight: 600;
}
.win{
    color: green;
}
.lose{
    color: red;

}
.resBtn{
    border: none;
    background-color: inherit;
    cursor: pointer;
    font-size: inherit;
    font-weight: inherit;
    color: darkred;
    text-decoration: underline;
}

.board{
    margin: 0 auto auto ;
    grid-area: 2 / 2 / 3 / 3;
    aspect-ratio: 1/1;
    /* height: min(700px, 90vh); */
    height: 100%;
    border: 5px solid #000000;
    display: grid;
    grid-template: repeat(8, 1fr) / repeat(8, 1fr);
}
.tile{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
}
.knight{
    position: absolute;
    aspect-ratio: 1/1;
    height: 80%;
    user-select: none;
}

.dragging{
    border: 1px solid red;
}
.start{
    background-color: yellow !important;
}

.glowing{
    background-color: red !important;
}
.over{
    border: 2px solid black;
}
.destination{
    background-color: green !important;
}
.destination.glowing{
    background-color: orange !important;
}
.disabled{
    cursor: no-drop;
}


.tile::before, .tile[pos-x = '1'][pos-y = '1']::after{
    content: attr(data-label);
    text-align: center;
    position: absolute;
}
.tile[pos-x = '1']::before{
    top: min(-30px, -30%);
}
.tile[pos-y = '1']:not(.tile[pos-x = '1'])::before{
    left: min(-25px, -30%);
}
.tile[pos-x = '1'][pos-y = '1']::after{
    left: min(-25px, -30%);
